.preloader {
  width: 100%;
  height: 100%;
  transition: opacity var(--transition-duration) var(--transition-timing), visibility var(--transition-duration) var(--transition-timing);
  position: fixed;
  top: 0;
  left: 0;
}

.preloader--hide {
  opacity: 0;
  visibility: hidden;
}

.preloader__animation {
  height: 100%;
  background: linear-gradient(0deg, #ff6b02 0%, #fd1d1d 40%, #fd1d1d 67%, rgba(253, 29, 29, .4) 100%);
  overflow: hidden;
}

.preloader__loader {
  height: 40px;
  width: 40px;
  margin-top: -20px;
  margin-left: -20px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader__loader:before, .preloader__loader:after {
  content: "";
  height: 40px;
  width: 40px;
  border: 8px solid rgba(255, 255, 255, .5);
  border-radius: 10px;
  position: absolute;
  top: 0;
}

.preloader__loader:before {
  animation: animate 2s linear infinite;
}

.preloader__loader:after {
  animation: animate2 2s linear infinite;
}

@keyframes animate {
  100% {
    transform: rotate(180deg)skew(360deg);
  }
}

@keyframes animate2 {
  100% {
    transform: rotate(-180deg)skew(-360deg);
  }
}

