.change_theme {
  max-width: 30em;
  box-shadow: rgba(255, 255, 255, .1);
  fill: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 180px;
  transform: scale(.75);
}

.change_theme:hover, .change_theme:focus {
  fill: var(--orange-hover);
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .change_theme {
    top: 160px;
    transform: scale(1);
  }
}

button.change_theme[data-theme="onDark"] {
  display: block;
}

button.change_theme[data-theme="onLight"] {
  display: none;
}

.dark__theme {
  background-color: #5c5b5b;
}

.dark__theme .footer {
  background-color: #363636;
}

.dark__theme .footer__text, .dark__theme .film__title {
  color: var(--white-color);
}

.dark__theme .infoFilm {
  background-color: #5c5b5b;
}

.dark__theme .infoFilm__button {
  fill: var(--light-gray-color);
  transition: fill var(--transition-duration) var(--transition-timing);
}

.dark__theme .infoFilm__button:hover, .dark__theme .infoFilm__button:focus {
  fill: var(--accent-color);
}

.dark__theme .button-modal__orange {
  color: var(--light-gray-color);
}

.dark__theme .button-modal__white {
  color: var(--light-gray-color);
  border-color: var(--light-gray-color);
}

.dark__theme .about-film__left {
  color: var(--light-gray-color);
}

.dark__theme .votes-count {
  background-color: var(--light-gray-color);
}

.dark__theme .ourteam__container {
  background-color: #5c5b5b;
}

.dark__theme .modal__close-button {
  fill: var(--light-gray-color);
  transition: fill var(--transition-duration) var(--transition-timing);
}

.dark__theme .modal__close-button:hover, .dark__theme .modal__close-button:focus {
  fill: var(--accent-color);
}

.dark__theme .ourteam__title, .dark__theme .ourteam__subtitle {
  color: var(--light-gray-color);
}

.dark__theme .authorization {
