}

.dark__theme .authorization__close {
  fill: var(--light-gray-color);
  transition: fill var(--transition-duration) var(--transition-timing);
}

.dark__theme .authorization__close:hover, .dark__theme .authorization__close:focus {
  fill: var(--accent-color);
}

.dark__theme .authorization__title {
  color: var(--light-gray-color);
}

.dark__theme .form__label {
  background-color: rgba(0, 0, 0, 0);
  top: -20px;
}

.dark__theme .form__input {
  background-color: #cec7c7;
}

.dark__theme .form__button {
  border-color: var(--white-color);
  transition: background-color var(--transition-duration) var(--transition-timing);
  background-color: rgba(0, 0, 0, 0);
}

.dark__theme .form__button:hover, .dark__theme .form__button:focus {
  background-color: var(--accent-color);
}

.dark__theme .registration__close {
  fill: var(--light-gray-color);
  transition: fill var(--transition-duration) var(--transition-timing);
}

.dark__theme .registration__close:hover, .dark__theme .registration__close:focus {
  fill: var(--accent-color);
}

.dark__theme .form__text {
