}

.dark__theme .paginationBox > li {
  color: var(--white-color);
  transition: transform var(--transition-duration) var(--transition-timing), color var(--transition-duration) var(--transition-timing);
}

.dark__theme .paginationBox > li:hover, .dark__theme .paginationBox > li:focus {
  color: var(--accent-color);
  transform: scale(1.02);
}

.dark__theme .paginationBox > li.paginationPage--current {
  color: #363636;
  background-color: var(--accent-color);
}

.dark__theme .paginationBox > li.paginationPage--current:hover, .dark__theme .paginationBox > li.paginationPage--current:focus {
  color: var(--white-color);
  transform: scale(1);
}

.dark__theme .paginationRow {
